<template>
	<v-layout row>
		<v-flex xs12>
			<v-slide-y-transition>
				<v-subheader v-if="isSearchVisible">
					<v-text-field append-icon="search" type="text" name="search" placeholder="..."></v-text-field>
				</v-subheader>
			</v-slide-y-transition>
			<v-expansion-panel>
				<v-expansion-panel-content v-for="(project,i) in projects" :key="i">
					<div slot="header">{{project.itemName}}</div>
					<v-card>
						<v-list two-line>
							<template v-for="(subProject, index) in project.itemData">
								<router-link
									:key="subProject.pj_code"
									avatar
									ripple
									@click="toggle(index)"
									:to="'room'" 
									tag="v-list-tile"
								>
									<v-list-tile-content>
										<v-list-tile-title>{{ subProject.pj_name }}</v-list-tile-title>
										<v-list-tile-sub-title class="text--primary">{{ subProject.short_name }}</v-list-tile-sub-title>
										<!-- <v-list-tile-sub-title>{{ project.pro }}</v-list-tile-sub-title> -->
									</v-list-tile-content>
								</router-link>
								<v-divider
									v-if="index + 1 < project.itemData.length"
									:key="index"
								></v-divider>
							</template>
						</v-list>
					</v-card>
				</v-expansion-panel-content>
			</v-expansion-panel>
    </v-flex>
  </v-layout>
</template>

<script>
	import { mapActions } from 'vuex'
	import { mapGetters } from 'vuex'
	import db from '../../database'
	import axios from '../../axiosRoomInspection'
	
	export default {
		data() {
			return {
				// projects: [{
				// 		projectId: 1,
				// 		projectCode: '048011',
				// 		projectName: ' 3',
				// 		projectNickname: 'Lpark RS-RM3'
				// 	},
				// 	{
				// 		projectId: 2,
				// 		projectCode: '048012',
				// 		projectName: ' 4',
				// 		projectNickname: 'Lpark RS-RM4'
				// 	},
				// 	{
				// 		projectId: 3,
				// 		projectCode: '048013',
				// 		projectName: ' 5',
				// 		projectNickname: 'Lpark RS-RM5'
				// 	},
				// 	{
				// 		projectId: 4,
				// 		projectCode: '048014',
				// 		projectName: ' 6',
				// 		projectNickname: 'Lpark RS-RM6'
				// 	}
				// ],
				// itemList: [{
				// 		itemListId: 1,
				// 		itemName: '',
				// 		itemFilter: 'Condo'
				// 	},
				// 	{
				// 		itemListId: 2,
				// 		itemName: '',
				// 		itemFilter: ''
				// 	},
				// ]
			}
		},
		created() {
			this.changeTitle('')
			this.changePreviousRoute('menu')
			// console.log('created',this.$store.state.appTitle);
		},
		mounted() {
			
			this.refreshToken()
			.then(res => { 	
				if (res === 'refreshed') {
					var localStorageObject = {};
					var lastUpdateDate = ''
					if(localStorage.getItem('localStorageObject')){
						var localStorageObject = JSON.parse(localStorage.getItem('localStorageObject'));
						lastUpdateDate = localStorageObject.roomInspection.lastUpdateDate
						console.log('Last updateDate date from local storage',lastUpdateDate)
					} 
					
					if(!('roomInspection' in localStorageObject)){
						localStorageObject.roomInspection = {lastUpdateDate: moment().format('YYYY-MM-DD')}
					} else if (!('lastUpdateDate' in localStorageObject.roomInspection)){
						localStorageObject.roomInspection.lastUpdateDate = moment().format('YYYY-MM-DD')
					}

					
					/* localStorage.setItem('localStorageObject', JSON.stringify(localStorageObject));
					*/

					//alert(lastUpdateDate)
					// const localLastUpdateDate = localStorage.getItem('lastUpdateDate')
					// const lastUpdateDate = JSON.parse(localLastUpdateDate)
					// getProject
					const getProjectsOptions = {
						method: 'GET',
						headers: {
							'Authorization': localStorage.getItem('jwt')
						},
						url: '/getProject'
					}
					axios(getProjectsOptions)
					.then(res => {
						const data = res.data.data
						//  get 
						if (res.data.code == 1) {
							// clear  IndexedDB Table Project 
							db.projects.clear()
							.then(res => {
								//  API  IndexedDB Table Project 
								db.projects.bulkAdd(data)
								.then(
									// 
									db.transaction("rw", db.projects, () => {
										const promise = db.projects.toArray()
										.catch('DataError', err => {
											console.log('DataError',err)
										})
										return promise
									})
									.then(res => {
										this.setProjects(res) // store.state
									})
								)
								.catch(db.BulkError, err => {
									console.error("project fail to insert " + e.failures.length + " records");
								})
							})
							.catch(err => {
								console.error('Error could not clear projects table: ',err)
							})
						} else {
							console.log(data);
						}
					})
					.catch(error => {
						console.error(error);
					})

					// getLocation
					var postData = {} 
					if( lastUpdateDate ){
						postData = {last_update_date: lastUpdateDate}
					}
					const getLocationsOptions = {
						method: 'POST',
						headers: {
							'Authorization': localStorage.getItem('jwt')
						},
						data: postData,
						url: '/getMasterLocation'
					}
					axios(getLocationsOptions)
					.then(res => {
						const data = res.data.data
						console.log(data)
						//  get 
						if (res.data.code == 1) {
							//  API  IndexedDB Table Locations
							data.forEach(location => {
								db.locations.put(location)
								.then(res => {
									console.log(res)
								})
								.catch(err => {
									console.log(err)
								})
							})
							db.transaction("rw", db.locations, () => {
								const promise = db.locations.toArray()
								.catch('DataError', err => {
									console.log('DataError',err)
								})
								return promise
							})
							.then(res => {
								this.setLocations(res) // store.state
							})
						} else {
							console.log(data);
						}
					})
					.catch(error => {
						console.error(error);
					})
				}
				
			})
			
		},
		computed: {
			isSearchVisible() {
				// console.log('computed',this.$store.getters.appTitle)
				return this.$store.getters.searchVisible
			},
			projects() {
				const projects = this.$store.getters['roomInspection/getProjects']
				var projectCondo = []
				var projectOther = []
				var projectAll = []
				projects.forEach(project => {
					if(project.pj_type === 'Condo'){
						projectCondo.push(project)
					} else {
						projectOther.push(project)
					}
				})
				projectAll = [
					{
						itemListId: 1,
						itemName: '',
						itemData: projectCondo
					},
					{
						itemListId: 2,
						itemName: '',
						itemData: projectOther
					}
				]
				return projectAll
			}
		},
		methods: {
			...mapActions({
				changeTitle: 'setTitle',
      			changePreviousRoute: 'setPreviousRoute'
				
			}),
			...mapActions('roomInspection',{
				setProjects: 'setProjects'
			})
		}
	}
</script>
