import Dexie from 'dexie'

const db = new Dexie('roomInspection')

db.version(2).stores({
  projects: 'pj_code,pj_name,short_name,ph_type,bd_date,bd_datemax,open_date,pj_group,pj_group_cs,pj_group_short'
})
db.version(3).stores({
  projects: 'pj_code,pj_name,short_name,ph_type,bd_date,bd_datemax,open_date,pj_group,pj_group_cs,pj_group_short',
  locations: 'loc_runno,loc_code,loc_type,loc_tname,loc_ename,create_by,update_date'
})

export default db
